import React from "react";
import api from "../api";

export default class extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const { page } = this.props;
    api
      .byPage({
        labels: page,
        type: 'PAGE'
      })
      .then(data => {
        console.log(data);
      });
  }

  render() {
    return <div className="loading">Loading...</div>;
  }
}
